export default {
  name: "kakao-link",
  props: {
    dialogKakao: Boolean,
    voteMatch: Object, //     vo
    vote: Object, //     vo
    updateVoteMatch: Object, //     vo
    confirmedVote: Object //     vo
  },
  data() {
    return {};
  },
  methods: {
    // U02-2
    writeLink() {
      let title = "";
      let desc = "";
      let webUrl = this.$http;
      let mobileWebUrl = this.$http;
      let btnTitle = "";
      let teamId = JSON.parse(sessionStorage.getItem("userInfo")).teamId;
      if (this.voteMatch) {
        //    .
        title = this.voteMatch.matchSchedule.matchType + "   ";
        desc = this.voteMatch.dueDate.slice(0, -2) + " .";
        webUrl += "/loginSignin?destination=voteMatch&teamId=" + teamId;
        mobileWebUrl += "/loginSignin?destination=voteMatch&teamId=" + teamId;
        btnTitle = " ";
      } else if (this.confirmedVote) {
        //    
        title =
          this.confirmedVote.matchSchedule.startTime.slice(0, -3) +
          "   ";
        desc = "";
        webUrl += "/loginSignin?destination=schedule&teamId=" + teamId;
        mobileWebUrl += "/loginSignin?destination=schedule&teamId=" + teamId;
        btnTitle = "  ";
      } else if (this.updateVoteMatch) {
        //    
        title = this.updateVoteMatch.matchSchedule.matchType + "   ";
        desc = this.updateVoteMatch.dueDate.slice(0, -2) + " .";
        webUrl += "/loginSignin?destination=voteMatch&teamId=" + teamId;
        mobileWebUrl += "/loginSignin?destination=voteMatch&teamId=" + teamId;
        btnTitle = " ";
      } else {
        //    
        title = this.vote.matchSchedule.matchType + "   ";
        webUrl += "/loginSignin?destination=voteMatch&teamId=" + teamId;
        mobileWebUrl += "/loginSignin?destination=voteMatch&teamId=" + teamId;
        btnTitle = "  ";
      }

      this.shareLink(title, desc, webUrl, mobileWebUrl, btnTitle);
    },

    // U02-3
    shareLink(title, desc, webUrl, mobileWebUrl, btnTitle) {
      window.Kakao.Link.sendDefault({
        objectType: "feed",
        content: {
          title: title,
          description: desc,
          imageUrl: "@/assets/emblem/FC201018.png",
          link: {
            webUrl: webUrl,
            mobileWebUrl: mobileWebUrl
          }
        },
        buttons: [
          {
            title: btnTitle,
            link: {
              webUrl: webUrl,
              mobileWebUrl: mobileWebUrl
            }
          }
        ]
      });
      window.location.reload();
    },

    //  
    close() {
      window.location.reload();
    }
  }
};
