<template>
  <div>
    <!--  |   -->
    <v-row
      id="vote-btn"
      class="mx-0 mt-0 pt-0"
      style="color:rgba(235, 255, 0,0.7)"
    >
      <v-col class="float-left">
        <span @click="controlMemberList(i)" style="cursor:pointer;"
          >  </span
        >
      </v-col>
      <v-col>
        <span
          class="float-right"
          style="cursor:pointer;"
          @click="controlFriendList(i)"
          v-if="
            vote.voteMatchSetting.friendEmp == 1 &&
              vote.voteStatus == 0 &&
              !vote.isFriend
          "
          > </span
        >
      </v-col>
    </v-row>
    <!--  |    -->
    <!--  -->
    <v-expand-transition>
      <div v-if="activeMemberList == i && openType == 0">
        <v-divider></v-divider>
        <!--  -->
        <v-row class="justify-space-between mx-5 mt-4">
          <div>: {{ vote.totalNum }}</div>
          <div>: {{ vote.votedNum }}</div>
          <div>: {{ vote.abscentNum }}</div>
          <div>: {{ vote.attendNum }}</div>
        </v-row>
        <v-row justify="end" class="mx-5 my-3">
          <div>: {{ vote.friendNum }}</div>
        </v-row>
        <v-divider></v-divider>
        <v-row justify="end" class="mx-5 mt-3 mb-5">
          <div> : {{ vote.totalAttend }}</div>
        </v-row>
        <!--   -->
        <v-row class="text-center mx-5">
          <v-col cols="10">
            /
          </v-col>
          <v-col cols="2">
            /
          </v-col>
        </v-row>
        <v-divider></v-divider>
        <v-row
          v-for="(result, i) in vote.voteMatchResults"
          :key="i"
          class="text-center mx-5"
        >
          <v-col cols="10">
            {{ showNickEmail(result) }}
          </v-col>
          <v-col cols="2">
            {{ result.attendance | attendanceFliter }}
          </v-col>
        </v-row>
      </div>
    </v-expand-transition>
    <!--   -->
    <!--   -->
    <v-expand-transition>
      <div v-if="activeFriendList == i && openType == 1">
        <v-divider></v-divider>
        <v-row class="pt-3">
          <v-col class="pr-0 pt-0" cols="10">
            <v-text-field
              class="ma-0 pa-0"
              v-model="inputEmail"
              placeholder="  ."
              @keydown.enter="searchFriend"
              dense
            />
          </v-col>
          <v-col cols="1" class="pt-1 pr-0 mx-0">
            <i class="material-icons md-18" @click.stop="searchFriend"
              >search</i
            >
          </v-col>
          <v-col cols="1" class="pa-0 pt-1">
            <i class="material-icons md-18" @click="activeFriendList = null"
              >close</i
            >
          </v-col>
        </v-row>
        <v-row class="text-center">
          <v-col offset="2" cols="4">
            e-mail
          </v-col>
          <v-col cols="2">
            
          </v-col>
        </v-row>
        <v-row class="mx-3 mb-3">
          <v-divider color="white"></v-divider>
        </v-row>
        <div v-if="!loading && !empty">
          <v-row
            id="friend-list"
            class="text-center my-1"
            v-for="(friend, j) in friends"
            :key="j"
          >
            <v-col offset="2" cols="4">
              {{ friend.email }}
            </v-col>
            <v-col cols="2">
              {{ friend.name }}
            </v-col>
            <v-col cols="1" class="ma-0 mb-3 pa-0 text-center"
              ><v-btn
                class="ma-0 ml-8 pa-0"
                elevation="2"
                color="#6920A3"
                @click="inviteFriend(friend.email, vote.voteMatchId)"
                style="font-size: 0.65em"
                ></v-btn
              >
            </v-col>
          </v-row>
        </div>
        <!-- progress circle -->
        <div v-else-if="loading">
          <progress-circle></progress-circle>
        </div>
        <!-- progress circle  -->
        <!-- empty -->
        <empty :ment="' '" v-else></empty>
        <!-- empty  -->
      </div>
    </v-expand-transition>
    <!--    -->
  </div>
</template>

<script>
export default {
  name: "friend-member-list",
  props: {
    vote: Object,
    i: Number
  },
  data() {
    return {
      //    
      inputEmail: null, // 
      friends: [],
      activeMemberList: null,
      activeFriendList: null,
      openType: null,

      //  
      loading: false,
      errored: false,
      empty: false
    };
  },
  computed: {},
  methods: {
    // V04-1
    searchFriend() {
      this.empty = false;
      this.$axios
        .get(
          "/user/1?email=" +
            this.inputEmail +
            "&teamId=" +
            JSON.parse(sessionStorage.getItem("userInfo")).teamId
        )
        .then(response => {
          this.loading = true;
          this.friends = response.data;
          if (this.friends.length == 0) {
            this.empty = true;
          }
        })
        .catch(() => {
          this.errored = true;
        })
        .finally(() => {
          setTimeout(() => {
            this.loading = false;
          }, 1000);
        });
    },
    // V04-2
    inviteFriend(email, voteMatchId) {
      let userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
      let content =
        userInfo.name + " " + userInfo.teamName + "  .";
      content += "  .";
      let notice = {
        giverTeam: {
          teamId: userInfo.teamId
        },
        giverUser: {
          email: userInfo.email
        },
        takerUser: {
          email: email
        },
        voteMatch: {
          voteMatchId: voteMatchId
        },
        content: content
      };
      notice.takerUsers = [];
      notice.takerUsers.push(notice.takerUser);
      this.$axios
        .post("/notice/1", notice)
        .then(() => {
          alert(" .");
          this.dialogKakao = true;
        })
        .catch(() => {
          alert(" .");
        });
    },
    //  ,   
    showNickEmail(result) {
      if (result.teamMember) {
        // 
        return result.teamMember.nickName;
      } else {
        //
        return result.user.email;
      }
    },
    //   
    controlMemberList(i) {
      if (this.activeMemberList == i) this.activeMemberList = null;
      //    
      else {
        if (this.activeFriendList == i) this.activeFriendList = null; //     
        this.activeMemberList = i;
        this.openType = 0;
      }
    },
    //    
    controlFriendList(i) {
      if (this.activeFriendList == i) this.activeFriendList = null;
      //    
      else {
        if (this.activeMemberList == i) this.activeMemberList = null; //     
        this.activeFriendList = i;
        this.openType = 1;
      }
    }
  },
  filters: {
    attendanceFliter(value) {
      if (value == 1) {
        //
        return "";
      } else if (value == 0) {
        // 
        return "";
      } else {
        return "";
      }
    }
  }
};
</script>

<style></style>
